import { generateUrl } from '@nextcloud/router';
import Close from 'vue-material-design-icons/Close.vue';
import Cloud from 'vue-material-design-icons/Cloud.vue';
import EmailArrowRightOutlineIcon from 'vue-material-design-icons/EmailArrowRightOutline.vue';
export default {
  name: 'ComposerAttachment',
  components: {
    Close,
    Cloud,
    EmailArrowRightOutlineIcon
  },
  props: {
    attachment: {
      type: Object,
      required: true
    },
    uploading: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      progress: 0,
      sizeString: '',
      finished: false
    };
  },
  computed: {
    previewURL() {
      if (this.attachment.hasPreview && this.attachment.id > 0) {
        return generateUrl("/core/preview?fileId=".concat(this.attachment.id, "&x=100&y=100&a=0"));
      }
      return '';
    },
    getIcon() {
      return OC.MimeType.getIconUrl(this.attachment.fileType);
    },
    extension() {
      return this.attachment.fileName.split('.').pop();
    }
  },
  methods: {
    onDelete(attachment) {
      this.$emit('on-delete-attachment', attachment);
    }
  }
};